<template>
  <div class="healthWarp">
    <HEADER></HEADER>
    <div class="con">
      <transition name="fade"
      @enter="enter"
      @before-leave="beforeLeave"
      @leave="leave">
      <div class="canvas" @click="toIndi" v-show="selected === '1'">
        <img :src="require('../../assets/'+imageType+'.jpg')" alt="" height="100%">
        <div id="mask">
          <p :class="{health: indexHealth && indexHealth.hepatobiliary && indexHealth.hepatobiliary.abnormal}">
            <img src="../../assets/warning.png" alt="" width="14px" height="14px" v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.hepatobiliary.abnormal">
            {{indexHealth && indexHealth.hepatobiliary && indexHealth.hepatobiliary.name}}&nbsp;
            <span v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.hepatobiliary.abnormal">()</span>
          </p>
          <p :class="{health: indexHealth && indexHealth.hepatobiliary && indexHealth.kidney.abnormal}">
            <img src="../../assets/warning.png" alt="" width="14px" height="14px" v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.kidney.abnormal">
            {{indexHealth && indexHealth.hepatobiliary && indexHealth.kidney.name}}&nbsp;
            <span v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.kidney.abnormal">()</span>
          </p>
          <p :class="{health: indexHealth && indexHealth.hepatobiliary && indexHealth.endocrine.abnormal}">
            <img src="../../assets/warning.png" alt="" width="14px" height="14px" v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.endocrine.abnormal">
            {{indexHealth && indexHealth.hepatobiliary && indexHealth.endocrine.name}}&nbsp;
            <span v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.endocrine.abnormal">()</span>
          </p>
          <p :class="{health: indexHealth && indexHealth.hepatobiliary && indexHealth.circulation.abnormal}">
            <img src="../../assets/warning.png" alt="" width="14px" height="14px" v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.circulation.abnormal">
            {{indexHealth && indexHealth.hepatobiliary && indexHealth.circulation.name}}&nbsp;
            <span v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.circulation.abnormal">()</span>
          </p>
          <p :class="{health: indexHealth && indexHealth.hepatobiliary && indexHealth.urinary.abnormal}">
            <img src="../../assets/warning.png" alt="" width="14px" height="14px" v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.urinary.abnormal">
            {{indexHealth && indexHealth.hepatobiliary && indexHealth.urinary.name}}&nbsp;
            <span v-show="indexHealth && indexHealth.hepatobiliary && indexHealth.urinary.abnormal">()</span>
          </p>
        </div>
        <div class="scorewarp">
          <div class="score">
            <div class="content">
              {{indexHealth && indexHealth.hepatobiliary && indexHealth.score}}<span></span>
            </div>
          </div>
          <div class="detehistory" @click.stop="toDete">
            <div></div>
            <div></div>
          </div>
        </div>
      </div>
      </transition>
      <div class="resultWarp" >
        <mt-navbar v-model="selected">
          <mt-tab-item id="1"></mt-tab-item>
          <mt-tab-item id="2"></mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <mt-tab-container v-model="selected" :swipeable="swipeable">
          <mt-tab-container-item id="1">
            <div class="resultText">
              {{indexHealth && indexHealth.hepatobiliary && indexHealth.summary}}
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="line" :style="{height: lineHeight + 'px'}">
              <v-chart :options="line" :auto-resize="true" ref="scopeline"/>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
      <div class="footer">
        <mt-button class="resultBtn" type="primary" size="large" @click.native="toRepo"></mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import HEADER from '@/components/header.vue'
import ECharts from 'vue-echarts/components/ECharts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/markPoint'
import { mapState } from 'vuex'
import { MessageBox } from 'mint-ui';
import util from '../../common/util'
import {
  loginUsingGET,
  getHistoryDetialUsingPOST,
  getTestResultHistoryUsingGET
  } from '@/../client/javascript/vue-api-client.js'

  export default {
    name: 'index',
    components: {
      HEADER,
      'v-chart': ECharts
    },
    data() {
      return {
        lineHeight: null,
        userid: 3,
        MessageBoxRead: this.$store.state.messageBox.read,
        imageType: 'man',
        selected: '1',
        swipeable: true,
        line: {
          xAxis: {
              type: 'category',
              data: [],
              show: false
          },
          yAxis: {
              type: 'value',
              show: false
          },
          series: [{
              data: [],
              type: 'line',
              smooth: true,
              markPoint:{
                large: true,
                effect: {
                  show: false,
                  shadowBlur: 0
                },
                data:[
                  {name: '1', value: 100, xAxis: 6, yAxis: 1320},
                ]
              },
          }],
        }
      }
    },
    computed: mapState({
      indexHealth: state => {
        return state.indexHealth
      }
    }),
    methods: {
      toRepo () {
        getHistoryDetialUsingPOST ({
          param: {
            resultId: this.$store.state.healthDetail.resultId,
            viewType: "urine_details"
          }
        }).then(response => {
          if(response.data.code === 0){
            this.$store.commit('setReportHealth', response.data.body)
            this.$router.push({name: 'repo', params: {goto: 'repo'}});
          }
        })
      },
      toDete () {
        this.$router.push({name: 'detehistory', params: {goto: 'detehistory'}});
      },
      toIndi () {
        getHistoryDetialUsingPOST ({
          param: {
            resultId: this.$store.state.healthDetail.resultId,
            viewType: "urine_desease_analysis"
          }
        }).then(response => {
          if(response.data.code === 0){
            this.$store.commit('setIndicatorHealth', response.data.body)
            this.$router.push({name: 'indicators', params: {goto: 'indicators'}});
          }
        })
      },
      login () {
        let that = this
        loginUsingGET ({
          id: this.userid
        }).then(response => {
          if(response.data.code === 0){
            that.$store.commit('setLoginState', true)
            console.log('')
            // this.$toast('')
            that.getHistoryDetialUsingPOST()
          }
        })
      },
      getHistoryDetialUsingPOST () {
        let that = this
        getHistoryDetialUsingPOST ({
          param: {
            resultId: this.$store.state.healthDetail.resultId,
            viewType: "urine_summary"
          }
        }).then(response => {
          if(response.data.code === 0){
            that.$store.commit('setIndexHealth', response.data.body)
          }
        })
      },
      deteHistory() {
        getTestResultHistoryUsingGET ({}).then(response => {
          if(response.data.code === 0){
            // this.list = response.data.list
            let o = this.lineDataFormer(response.data.list)
            this.line = {
              xAxis: {
                type: 'category',
                data: o.x,
                show: false
              },
              yAxis: {
                type: 'value',
                show: false
              },
              series: [{
                data: o.y,
                type: 'line',
                smooth: true,
                markPoint:{
                  large: true,
                  effect: {
                    show: false,
                    shadowBlur: 0
                  },
                  data:[
                    {name: '1', value: o.y[o.y.length-1], xAxis: o.y.length-1, yAxis: o.y[o.y.length-1]},
                  ]
                },
              }],
            }
            this.$refs.scopeline.hideLoading();
            this.$refs.scopeline.mergeOptions(this.line)
          }
        })
      },
      lineDataFormer(data) {
        let x = []
        let y = []
        for(let i in data) {
          x.push(util.formatDate.format(new Date(data[i].gmtCreate)))
          y.push(data[i].resultId)
        }
        return {
          x: x,
          y: y
        }
      },
      formatDate (time) {
        return util.formatDate.format(new Date(time), 'yyyy-MM-dd hh:mm:ss')
        // return t.getFullYear()+"-"+t.getMonth()+"-"+t.getDate()+"  "
        // +t.getHours()+":"+t.getMinutes()+":"+t.getSeconds()
      },
      enter(el, done){
      },
      beforeLeave(el,done){
          // this.bodyHeight = el.clientHeight;
      },
      leave(el, done) {
          // el.style.height = el.clientHeight + 'px';
      }
    },
    created () {
      // let mask = document.getElementById("mask");
      this.login()
    },
    mounted() {
      this.clientHeight = document.body.clientHeight
      this.lineHeight = document.body.clientHeight - 40 - 32 - 41 - this.clientHeight*0.0185
      if(!this.MessageBoxRead){
        MessageBox({
          title: '',
          message: ',',
          confirmButtonText: ''
        });
        this.$store.commit('readMessage', true)
      }
      this.deteHistory()
      this.$refs.scopeline.showLoading()
    }
  }
/* canvas */
</script>

<style lang="less">
.healthWarp {
  height: 100%;
  .con {
    height: 100%;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-around;
    background: rgb(235, 235, 235);
    .fade-enter-active, .fade-leave-active {
      transition: opacity .2s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
      opacity: 0;
    }
    .canvas {
      display: flex;
      flex: 3.5;
      flex-direction: column;
      margin-top: 40px;
      border-bottom: 2px rgb(240, 240, 240) solid;
      position: relative;
      #mask {
        width: 100%;
        height: 45%;
        // z-index: 2;
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 20%;
        p {
          width: 50%;
          color: rgb(65, 164, 138);
        }
        .health {
          color: #f4c177;
        }
      }
      .scorewarp {
        width: 100%;
        height: 35%;
        // z-index: 2;
        position: absolute;
        bottom: 0;
        .score {
          background: url('../../assets/scope.png') no-repeat;
          background-size: 100%;
          position: absolute;
          bottom: 2%;
          left: 4%;
          height: 8rem;
          width: 7rem;
          // z-index: 3;
          border-radius: 50%;
          display: flex;
          flex: 1;
          justify-content: center;
          align-items: center;
          .content {
            font-size: 2rem;
            color: rgb(65, 164, 138);
            display: contents;
            span {
              font-size: 1.3rem;
            }
          }
        }
        .detehistory {
          position: absolute;
          bottom: 8%;
          right: 8%;
          height: 5rem;
          width: 5rem;
          // z-index: 3;
          border: 1px solid rgb(55, 107, 152);
          border-radius: 50%;
          display: flex;
          flex: 1;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          font-size: 1.25rem;
          color: rgb(55, 107, 152)
        }
      }
    }
    .resultWarp {
      flex: 1.5;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      background: rgb(235, 235, 235);
      .mint-tab-container-item {
        background: white;
      }
      p {
        margin: 0;
      }
      .resultText {
        padding: 10% 0 6% 0;
        text-indent: 2rem;
        font-size: 1rem;
        text-align: left;
      }
      .line {
        width: 100%;
        min-height: 400px;
        .echarts {
          width: 100%;
          height: 100%;
          div {
            width: 100% !important;
            canvas {
              width: 100% !important;
            }
          }
        }
      }
      /* covered mint */
      .mint-navbar .mint-tab-item {
        padding: 10px 0;
      }
      .mint-tab-container {

      }
      .mint-tab-container-wrap {
        margin-top: 2.75%;
        height: 100%;
        background: white;
        .mint-tab-container-item {
          display: flex;
          justify-content: center;
          .mint-button--large {
            width: 90%;
          }
        }
      }
    }
    .footer {
      width: 100%;
      display: flex;
      justify-content: center;
      .mint-button--primary {
        background: rgb(55, 107, 152);
        border-radius: 8px;
      }
      .resultBtn {
        width: 90%;
      }
    }
  }
}
</style>



